import React, { useState } from "react";
import Header from "./Header";
import Main from "./Main";
import Footer from "./Footer";
import Sidebar from "./Sidebar";

import "./index.css";
import TrolleyContext from "./TrolleyContext";

function App() {
  const [trolleyCurrent, setTrolleyCurrent] = useState([]);
  const [trolleyImported, setTrolleyImported] = useState(false);

  return (
    <div>
      <TrolleyContext.Provider
        value={{
          trolleyCurrent,
          setTrolleyCurrent,
          trolleyImported,
          setTrolleyImported,
        }}
      >
        <Header />
        <div className="bodyWrapper">
          <Main />
          <Sidebar />
        </div>
      </TrolleyContext.Provider>
      <Footer />
    </div>
  );
}

export default App;
