import React, { useContext, useEffect, useState } from "react";
import TrolleyContext from "../TrolleyContext";
import { db } from "../firebase";
import { useAuth } from "../AuthContext";

function ProductCard({ productData }) {
  const {
    trolleyCurrent,
    setTrolleyCurrent,
    trolleyImported,
    setTrolleyImported,
  } = useContext(TrolleyContext);
  const { currentUser } = useAuth();

  function addQuantity(x) {
    for (let product of x) {
      if (productData.name === product.name) {
        return x.map((productAgain) =>
          product.name === productAgain.name
            ? { ...product, ...{ quantity: product.quantity + 1 } }
            : productAgain
        );
      }
    }
    return [...x, { ...productData, ...{ quantity: 1 } }];
  }
  function handleAddButton() {
    if (currentUser) {
    }
    setTrolleyCurrent((prevTrolley) => {
      return addQuantity(prevTrolley);
    });
  }

  useEffect(() => {
    if (trolleyImported && currentUser) {
      db.collection(currentUser.uid).doc("trolley").set({ trolleyCurrent });
    }
  }, [trolleyCurrent]);

  const savingText = productData.priceBefore && (
    <p className="productCardSavingText">
      Only £{productData.price.toFixed(2)}: Save £
      {(productData.priceBefore - productData.price).toFixed(2)}
    </p>
  );

  const sponsoredText = productData.sponsored && (
    <p className="productCardSponsoredText">Sponsored</p>
  );

  const deliveryText = productData.delivery && (
    <p className="productCardDeliveryText">
      Delivery by {productData.delivery}
    </p>
  );

  return (
    <div className="productCardContainer">
      {savingText}
      {sponsoredText}
      <img className="productCardImage" src={productData.src} />
      <a href="#">
        <p className="productCardNameText">{productData.name}</p>
      </a>
      <p className="productCardPriceText">
        <span>£{productData.price.toFixed(2)}</span>/unit
      </p>
      <button
        name={productData.name}
        className="productCardButton"
        onClick={handleAddButton}
      >
        Add
      </button>
      {deliveryText}
    </div>
  );
}

export default ProductCard;
